"use client";

import { useRouter } from "next/navigation"; // For the 'pages' directory
// If you're using the 'app' directory:
import { useParams } from "next/navigation";
import LogoutButton from "@/components/forms/LogoutButton";

export default function UserProfile() {
    const router = useRouter(); // Use this for `pages`-based routing
    // const params = useParams(); // Use this for `app`-based routing
    // const { id } = params; 

    // Destructure the `id` from the query
    const { id } = router.query;

    // Fetch the user data or display the user's name using the `id`
    return (
        <div>
            <h1>User Profile</h1>
            <p>Welcome, user with ID: {id}</p>
            <LogoutButton/>
        </div>
    );
}
